@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Itim&display=swap);
@import url(https://fonts.googleapis.com/css2?family=family=Itim&display=swap);
@import url(https://fonts.googleapis.com/css2?family=family=Itim&display=swap);
@import url(https://fonts.googleapis.com/css2?family=family=Itim&display=swap);
@import url(https://fonts.googleapis.com/css2?family=family=Itim&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.logo{
    display: flex;
    align-items: center;
}

.project-name{
    font-family: 'Dancing Script', cursive;
    font-size: 1.3em;
    font-weight: 600;
    color: #fefffd;
    margin-left: 0.3em;
}

nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #e8262e;
    padding: 20px 80px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.06);
}

#navbar{
    display: flex;
    align-items: center;
    justify-content: center;
}

#navbar li{
    list-style: none;
    padding: 0 20px;
    position: relative;
    font-family: 'Dancing Script', cursive;
}

#navbar li a{
    text-decoration: none;
    font-size: 1.3em;
    font-weight: 600;
    color: #fefffd;
    transition: 0.3s ease-in-out;
}

#navbar li a:hover,
#navbar li a.active{
    color: #fee01c;
}

#navbar li a:hover::after,
#navbar li a.active::after{
    content: "";
    width: 50px;
    height: 2px;
    background: #fee01c;
    position: absolute;
    bottom: -4px;
    left: 20px;
}

#mobile{
    display: none;
}

#mobile i{
    color: #fefffd;
}

@media screen and (max-width: 768px) {

    #navbar{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center; 
        position: fixed;
        top: 90px;
        right: -300px;
        width: 300px;
        height: 100vh;
        background: #e8262e;
        box-shadow: 0 40px 60px rgba(0, 0, 0, 0.6);
        padding: 40px 0 0 10px;
        transition: 0.3s ease-in-out;
        z-index: 999;
    }

    .scroll{margin: 0; height: 100%; overflow: hidden}


    #navbar.active{
        right: 0px;
    }

    #mobile{
        display: block;
    }

    #mobile i{
        font-size: 1.4em;
        cursor: pointer;
    }

    #navbar li{
        margin-bottom: 25px;
    }
}
.filter-name{
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Itim', cursive;
  margin-top: 0.3em;
}

.select-container{
  
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  
}
/*border: 3px solid green;*/
.select{
  background-color: #fefffd;
  min-width: 60%;
  font-family: 'Itim', cursive;
}
.pizza-row{
    background-color: #fefffd;
    border: 1px solid #ccc;
    border-radius: 5px;
    display: flex;
    align-items: stretch;
    flex-wrap: wrap;
    margin-bottom: 2.5em;
}


.pizza-image{
    flex-grow: 1;
    min-width: 20%;
    max-width: 20%;
    display: flex;
}


#image{
    border-radius: 3%;
}

.pizza-details{
    margin-top: 0.3em;
    flex-grow: 8;
    font-family: 'Itim', cursive;
}

.shop-button{
    margin-top: 2em;
    border-radius: 4px;
    border: 2px solid #fee01c;
    background-color: #fee01c;
    transition-duration: 0.4s;
    padding: 1.1em 2.2em;
    font-family: 'Itim', cursive;
}

button:hover{
    background-color: #fefffd;
}

button:active {
    background-color: #e4c601;
    border: 2px solid #e4c601;
    transition-duration: 0.2s;
}

@media screen and (max-width: 830px) {
    .pizza-row{
        flex-wrap: nowrap;
    }
    
    .pizza-image{
        flex-grow: none;
        min-width: none;
        max-width: none;
    }
    
    .pizza-details{
        display: flex;
        align-items: center;
        flex-direction: column;
        flex-grow: none;
        max-width: 68%;
        margin-top: 0.1em;
    }
    
    .shop-button{
        margin-top: 1em;
        padding: 0.5em 1em;
    }
}

@media screen and (max-width: 768px) {
    .pizza-row{
        flex-wrap: nowrap;
    }
    
    .pizza-image{
        flex-grow: none;
        min-width: none;
        max-width: none;
    }
    
    .pizza-details{
        display: flex;
        align-items: center;
        flex-direction: column;
        flex-grow: none;
        max-width: 58%;
        margin-top: 0.1em;
    }
    
    .shop-button{
        margin-top: 1em;
        padding: 0.5em 1em;
    }
}



@media screen and (max-width: 540px) {
    .pizza-details{
        max-width: 75%;
    }
}

@media screen and (max-width: 420px) {
    .pizza-details{
        max-width: 59%;
    }
}

@media screen and (max-width: 375px) {
    .pizza-details{
        max-width: 55%;
    }
    .shop-button{
        margin-top: 0.5em;
        padding: 0.5em 1em;
    }
}

@media screen and (max-width: 360px) {
    .pizza-details{
        max-width: 53%;
    }
}
.main-container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0.5em;
    margin-left: 4em;
    margin-right: 4em;
}

.filter{
    align-self: stretch;
    flex-grow: 1;
    min-width: 25%;
}

.pizza-list{
    flex-grow: 3;
    max-width: 75%;
    margin-left: 2.6em;
    margin-top: 0.4em;
}

@media screen and (max-width: 768px) {
    .main-container{
        flex-direction: column;
        margin-left: 1.25em;
        margin-right: 1.25em;
    }
    
    .pizza-list{
        min-width: 100%;
        margin-top: 2em;
        margin-right: 0;
        margin-left: 0;
    }
}

.buy-now{
    margin-top: 1em;
    margin-bottom: 0.5em;
    border-radius: 4px;
    border: 2px solid #fee01c;
    background-color: #fee01c;
    transition-duration: 0.4s;
    padding: 1.1em 2.2em;
    font-family: 'Itim', cursive;
}

.question{
    margin-top: 0.5em;
    margin-bottom: 0.3em;
}

.answer{
    text-align: center;
}

.about{
    font-family: 'Itim', cursive;
    padding-left: 3em;
    padding-right: 3em;
    margin-top: 1em;
}

hr{
    margin-top: 1.5em;
}



@media screen and (max-width: 768px) {
    .description{
        text-align: justify;
        text-justify: inter-word;
    }
    
    .about{
        padding-left: 1em;
        padding-right: 1em;
    }

    .answer{
        text-align: justify;
    }
}
table {
  border: 1px solid black;
  border-collapse: collapse;
  margin-top: 1.5em;
  padding: 0;
  width: 100%;
  table-layout: fixed;
  font-family: 'Itim', cursive;
}

table caption {
  font-size: 1.5em;
  margin: .5em 0 .75em;
}

table tr {
  background-color: #fefffd;
  border: 1px solid #000000;
  padding: .35em;
}

table th,
table td {
  padding: .625em;
  text-align: center;
  /*text-transform: capitalize;*/
}

table th {
  font-size: .85em;
  letter-spacing: .1em;
  text-transform: uppercase;
  background: #e8262e;
}

thead{
  
  color: #fee01c;
}

table:last-child {
  margin-bottom: 2em;
}


@media screen and (max-width: 600px) {
  table {
    border: 0;
    margin-top: 1em;
  }
  
  table caption {
    font-size: 1.3em;
  }
  
  table thead {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
  
  table tr {
    border-bottom: 3px solid #ddd;
    display: block;
    margin-bottom: .625em;
  }
  
  table td {
    border-bottom: 1px solid #e8262e;
    display: block;
    font-size: .8em;
    text-align: right;
  }
  
  table td::before {
    content: attr(data-label);
    float: left;
    font-weight: bold;
    text-transform: uppercase;
  }
  
  table td:last-child {
    border-bottom: 0;
  }
  
  table tr {
    border-bottom: 1px solid #e8262e;
    border-color: #e8262e;
  }
  
  table:last-child {
    margin-bottom: 1em;
  }
}










